* {
  box-sizing: border-box;
}

::backdrop {
  background-color: #000c;
}

body {
  grid-template-rows: auto 1fr auto;
  grid-template-areas: "header"
                       "main"
                       "footer";
  min-height: 100vh;
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Helvetica Neue, Arial, sans-serif;
  display: grid;
}

header {
  color: #fff;
  z-index: 1;
  background-color: #000;
  grid-area: header;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 0 3rem;
  padding: 0 2rem;
  display: flex;
  position: fixed;
}

main {
  grid-area: main;
  margin-top: 110px;
  padding: 1rem;
}

footer {
  color: #fff;
  background-color: #000;
  grid-area: footer;
  justify-content: center;
  align-items: center;
  margin: 3rem 0 0;
  padding: 1rem;
  display: flex;
}

header a, footer a {
  color: orange;
  font-weight: bold;
  text-decoration: none;
}

header a:hover, header a:active, footer a:hover, footer a:active {
  color: #ff0;
}

footer ul {
  justify-content: center;
  gap: 3rem;
  list-style: none;
  display: flex;
}

#search-summary {
  text-align: center;
  margin: 0 .5rem 1rem;
  font-size: .8em;
}

#productlist {
  flex-flow: wrap;
  justify-content: center;
  align-items: stretch;
  gap: 3rem 1.5rem;
  display: flex;
}

.label {
  font-weight: 700;
}
